.header{
    display: flex;
    justify-content: space-between;
}

.logo{
    height: 3rem;
    width: 10rem;
}
.header-menu{
    list-style: none;
    display: flex;
    gap: 2rem;
    color: #fff;
}

.header-menu>li:hover{
    color: var(--blue);
    cursor: pointer;
}

@media screen and (max-width:835px){
    .header>:nth-child(2){
        position: fixed;
        right: 2rem;
        z-index: 55;
    }
    .header-menu{
        flex-direction: column;
        background-color: var(--appColor);
        padding: 2rem;
    }
}